#centerDiv {
    width: 70%;
    max-width: 40rem;
    height: 500;
    color: white;
    margin: auto;
}

@media only screen and (max-width: 480px) {
    /* For Mobile */
    #centerDiv {
        width: 90%;
    }

    #title {
        font-size: 3.14rem;
    }

    .subTitle {
        font-size: 2.0rem !important;
    }
}